import { Button, Modal } from "react-bootstrap"


function ModalVideoKycSet(props) {
    return (
        <>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                backdrop="static"
            >
                <Modal.Header >
                    <Modal.Title id="contained-modal-title-vcenter" style={{ color: '2e3191' }}>
                        Video KYC Instructions / वीडियो केवाईसी निर्देश
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <p style={{ lineHeight: '37px', fontWeight: '700' }}>
                    My name is ……. My shop name is ……… I want to join NEEDU and I am sharing my KYC documents to complete the process. मेरा नाम ……… है। मेरी दुकान का नाम ………… है। मैं NEEDU के साथ जुड़ना चाहता/चाहती हूँ और अपनी KYC प्रक्रिया पूरी करने के लिए दस्तावेज़ साझा कर रहा/रही हूँ।
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={props.onHide} >Agree</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
export default ModalVideoKycSet